.sobremiEn{
    background-color: #1e2326;
    color: #fff;
    padding: 50px 20px;
}
.sobremiEn .contenido-seccion{
    max-width: 1100px;
    margin: auto;
}
.sobremiEn h2{
    font-size: 48px;
    font-family: 'Righteous';
    text-align: center;
    padding: 20px 0;

}
.sobremiEn .contenido-seccion p{
    font: 18px;
    line-height: 22px;
    margin-bottom: 20px;
}
.sobremiEn .contenido-seccion p span{
    color: rgb(96, 0, 96);
    font-weight: bold;
}
.sobremiEn .fila{
    display: flex;
}
.sobremiEn .fila .col
{
    width: 50%;
}
.sobremiEn .fila .col h3{
    font-size: 28px;
    font-family: 'Righteous';
    margin-bottom: 25px;
}
.sobremiEn .fila .col ul{
    list-style: none;
}
.sobremiEn .fila .col ul li{
    margin: 12px 0;
}
.sobremiEn .fila .col ul li strong{
    display: inline-block;
    color: rgb(96, 0, 96);
    width: 130px;
}
.sobremiEn .fila .col ul li span{
    background-color: rgb(96, 0, 96);
    padding: 3px;
    font-weight: bold;
    border-radius: 5px;
}
.sobremiEn .fila .col .contenedor-intereses{
    display: flex;
    flex-wrap: wrap;
}
.sobremiEn .fila .col .contenedor-intereses .interes{
    width: 100px;
    height: 100px;
    background-color: #252A2E;
    border-radius: 10px;
    margin: 0 15px 15px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: .3s;
}.sobremiEn .fila .col .contenedor-intereses .interes:hover{
    background-color: rgb(96, 0, 96);
}
.sobremiEn .fila .col .contenedor-intereses .interes i{
    font-size: 30px;
    margin-bottom: 10px;
}
.sobremiEn button{
    cursor: pointer;
    background-color: transparent;
    border: 2px solid #fff;
    width: fit-content;
    display: block;
    margin: 20px auto;
    padding: 10px 22px;
    font-size: 16px;
    color: #fff;
    position: relative;
    z-index: 10;
}
.sobremiEn button .overlay{
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
    background-color: rgb(96, 0, 96);
    z-index: -1;
    transition: 1s;
}
.sobremiEn button:hover .overlay{
    width: 100%;
}
@media screen and (max-width:700px){
    .sobremiEn .fila{
        display: block;
    }
    .sobremiEn .fila .col{
        width: fit-content;
    }
}